<template>
  <div class="header">
    <div class="up-wrapper">
      <!--  -->
      <!--  -->
      <div class="title" id="title"></div><div class="lower-title" id="lower-title"></div>
    </div>
    <!--  -->
    <vue-nav></vue-nav>
    <div class="next-nav-wrapper">
      <!--  -->
      <!--  -->
      <next-nav></next-nav>
    </div>
    <!--  -->
    <transition name="fadelogin">
      <div class="login-box-wrapper" v-show="isLogin">
        <login></login>
      </div>
    </transition>
  </div>
</template>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import 'Header.styl'
</style>

<script type="text/ecmascript-6">
// 
  import VueNav from '@/components/Nav/VueNav'
  import Login from '@/components/Login/Login'
  import NextNav from '@/components/NextNav/NextNav'
  import {loginMixin} from '@/common/js/mixin'

  export default {
    mixins: [loginMixin],
    components: {
      VueNav,
      Login,
      NextNav
    }
  }
</script>


