<template>
  <div class="border-article">
    <div class="border-title">{{contentTitle}}</div>
    <div class="border-line"></div>
    <slot></slot>
    <!-- slot -->
  </div>
</template>

<style lang="stylus" scoped>
  @import 'LeftBorder.styl'
</style>

<script script type="text/ecmascript-6">
// 
  export default {
    props: {
      contentTitle: {
        // 
        type: String,
        default: ''
      }
    }
  }
</script>





