import {mapMutations, mapGetters} from 'vuex'
import axios from 'axios'
import {decodeComment} from '@/common/js/util'
import xss from 'xss'

// mixin
export const loginMixin = {
  data () {
    return {
      textName: '',
      timer: null
    }
  },
  created () {
    if (this.loginSuccess) {
      this.textName = ''
    } else {
      this.textName = ''
    }
  },
  computed: {
    ...mapGetters([
      'currentUser',
      // 
      'isLogin',
      // 
      'loginSuccess'
      // 
    ])
  },
  methods: {
    // 
    isLoOrRe () {
      if (!this.loginSuccess) {
        this.isLo()
      } else {
        this.setCurrentUser({})
        this.setLoginSuccess(false)
        this.isFa()
      }
    },
    // 
    isLo () {
      this.setIsLoginToggle()
    },
    // 
    isOp () {
      this.moveTop()
      this.setIsLogin(true)
    },
    // 
    isFa () {
      this.setIsLogin(false)
    },
    // 
    fn () {
      var oTop = document.body.scrollTop || document.documentElement.scrollTop
      // document.body.scrollTop  webkit 
      // document.documentElement.scrollTop 
      if (oTop > 0) {
        document.body.scrollTop = document.documentElement.scrollTop = oTop - 50
        this.timer = requestAnimationFrame(this.fn)
        // requestAnimationFrame   setInterval
      } else {
        cancelAnimationFrame(this.timer)
        // 
      }
    },
    // 
    moveTop () {
      cancelAnimationFrame(this.timer)
      this.timer = requestAnimationFrame(this.fn)
    },
    ...mapMutations({
      setCurrentUser: 'SET_CURRENT_USER',
      setLoginSuccess: 'SET_LOGIN_SUCCESS',
      setIsLogin: 'SET_IS_LOGIN',
      setIsLoginToggle: 'SET_IS_LOGIN_TOGGLE'
    })
  },
  watch: {
    loginSuccess (newVal) {
      if (newVal) {
        this.textName = ''
      } else {
        this.textName = ''
      }
    }
  }
}

// 
export const turnArticleMixin = {
  computed: {
    ...mapGetters([
      'currentArticle'
    ])
  },
  methods: {
    // this.$router
    turnArticle () {
      this.setCurrentArticle(this.article)
      this.addVisit()
      this.$router.push(`/mainpage/article/${this.currentArticle.articleId}`)
    },
    // 
    addVisit () {
      let param = {
        articleId: this.currentArticle.articleId
      }
      let url = '/articlelist/visit'
      axios.get(url, {
        params: param
      }).then((response) => {
        let res = response.data
        if (res.status === '0') {
          this.setArticleVisit(this.currentArticle.articleVisit)
        } else {
          console.log(res.msg)
        }
      })
    },
    ...mapMutations({
      setCurrentArticle: 'SET_CURRENT_ARTICLE',
      setArticleVisit: 'SET_ARTICLE_VISIT'
    })
  }
}

// 
export const getDetailMixin = {
  data () {
    return {
      articleDetail: {}
    }
  },
  methods: {
    getArticleDetail (articleId) {
      let param = {
        articleId: articleId
      }
      axios.get('/articlelist/content', {
        params: param
      }).then((response) => {
        let res = response.data
        if (res.status === '0') {
          this.articleDetail = res.result
          // this.articleDetail.articleContent = decodeURIComponent(this.articleDetail.articleContent)
          this.articleDetail.articleComments.forEach((item) => {
            decodeComment(item)
          })
          return this.articleDetail
        } else {
          alert('')
        }
      })
    },
    returnArticleDetail () {
      return this.articleDetail
    }
  }
}

// 
export const collectMixin = {
  computed: {
    ...mapGetters([
      'currentArticle',
      'currentUser'
    ])
  },
  methods: {
    collect (articleId, userId) {
      let param = {
        articleId: articleId,
        userId: userId
      }
      axios.get('/users/collect', {
        params: param
      }).then((response) => {
        let ret = response.data
        if (ret.status === '0') {
          let temp = this.currentArticle.articleLove
          if (ret.msg === 'add') {
            temp++
          } else {
            temp--
          }
          this.setArticleLove(temp)
        }
      })
    },
    ...mapMutations({
      setArticleLove: 'SET_ARTICLE_LOVE',
      setCurrentUser: 'SET_CURRENT_USER',
      setCheckUser: 'SET_CHECK_USER'
    })
  }
}

export const searchMixin = {
  data () {
    return {
      searchText: ''
      // 
    }
  },
  computed: {
    ...mapGetters([
      'page',
      // 
      'search',
      // 
      'pageSize',
      // 
      'sort'
      // 
    ])
  },
  methods: {
    showSearch () {
      if (this.searchText !== '') {
        // 
        this.setPage(1)
        let search = {
          tag: 'text',
          name: xss(this.searchText)
          // xss
        }
        this.setSearch(search)
        let param = {
          page: this.page,
          pageSize: this.pageSize,
          search: this.search,
          sort: this.sort
        }
        let url = '/articlelist'
        // 
        // 
        axios.get(url, {
          params: param
        }).then((response) => {
          let res = response.data
          if (res.status === '0') {
            this.setArticleLists(res.result)
          } else {
            console.log(res.msg)
          }
        })
      }
    },
    ...mapMutations({
      setPage: 'SET_PAGE',
      setArticleLists: 'SET_ARTICLE_LISTS',
      setSearch: 'SET_SEARCH'
    })
  }
}



