<template>
  <div class="button-wrapper">
    <div class="check" @click="checkObj()" v-if="isCheck">{{text}}</div>
    <div class="turn" @click="turnObj()" v-if="isTurn">{{text}}</div>
    <div class="delete" @click="deleteObj()" v-if="isDelete">{{text}}</div>
    <div class="watch" @click="watchObj()" v-if="isWatch">{{text}}</div>
    <div class="collect" @click="collectObj()" v-if="isCollect">{{text}}</div>
    <div class="publish" @click="publishObj()" v-if="isPublish">{{text}}</div>
    <div class="loop" @click="loopObj()" v-if="isLoop !== 0"><span v-if="isLoop === 1"></span><span v-if="isLoop === 2"></span></div>
  </div>
</template>

<style lang="stylus" scoped>
  @import 'OperateButton.styl'
</style>

<script type="text/ecmascript-6">
// 
  export default {
    props: {
      isCheck: {
        type: Boolean,
        default: false
      },
      isTurn: {
        type: Boolean,
        default: false
      },
      isDelete: {
        type: Boolean,
        default: false
      },
      isWatch: {
        type: Boolean,
        default: false
      },
      isCollect: {
        type: Boolean,
        default: false
      },
      isPublish: {
        type: Boolean,
        default: false
      },
      isLoop: {
        type: Number,
        default: 0
      },
      text: {
        type: String,
        default: ''
      }
    },
    methods: {
      checkObj () {
        this.$emit('check')
      },
      turnObj () {
        this.$emit('turn')
      },
      deleteObj () {
        this.$emit('delete')
      },
      watchObj () {
        this.$emit('watch')
      },
      collectObj () {
        this.$emit('collect')
      },
      publishObj () {
        this.$emit('publish')
      },
      loopObj () {
        this.$emit('loop')
      }
    }
  }
</script>


