<template>
  <form class="login-box">
    <div class="message" v-text="messageText"></div>
    <div class="line"></div>
    <label class="username-wrapper"><div class="text"></div><input class="username" type="text" name="username" v-model="userName"></label>
    <div class="line"></div>
    <label class="password-wrapper"><div class="text"></div><input class="password" type="password" name="password" v-model="passWord"></label>
    <div class="line"></div>
    <div class="remember" v-show="!isRegister">
      <label class="remember-box"><div class="retext" @click.stop="isReUn()" :class="{'isre':isRememberUsername}"></div><input type="checkbox" class="checkbox" name="remember"></label>
      <label class="remember-box"><div class="retext" @click.stop="isRePw()" :class="{'isre':isRememberPassword}"></div><input type="checkbox" class="checkbox" name="remember"></label>
    </div>
    <label class="ymail-wrapper" v-show="isRegister"><div class="text"></div><input class="ymail" type="text" name="ymail" v-model="userEmail"></label>
    <div class="line"></div>
    <label class="register-wrapper" @click.stop="Register()"><div class="register"><span v-show="!isRegister"></span><span v-show="isRegister"></span></div></label>
    <div class="line"></div>
    <label class="yzm-wrapper">
      <span @click="newYZM()"><yan-zheng-ma ref="yzm" @yzm="setYZM"></yan-zheng-ma></span><span class="yzm"><input type="text" v-model="yzmText" placeholder="" class="yzm-input"></span>
    </label>
    <div class="line"></div>
    <label class="submit-wrapper" @click.stop.prevent="Login"><input class="submit" type="submit" name=""></label>
    <div class="line"></div>
    <div class="return" @click.stop="isFa()"><img src="./cross.svg" alt="" width="20px" height="20px"></div>
  </form>
</template>

<style lang="stylus" scoped>
  @import 'Login.styl'
</style>

<script type="text/ecmascript-6">
// 
  import axios from 'axios'
  import storage from 'good-storage'
  import {loginMixin} from '@/common/js/mixin'
  import {storageKey} from '@/common/js/config'
  import YanZhengMa from '@/components/YanZhengMa/YanZhengMa'
  import xss from 'xss'
  import {isPassword, isEmail} from '@/common/js/util'

  export default {
    mixins: [loginMixin],
    data () {
      return {
        isRememberUsername: false,
        // 
        isRememberPassword: false,
        // 
        isRegister: false,
        // 
        userName: '',
        // 
        passWord: '',
        // 
        userEmail: '',
        // 
        messageText: '',
        // 
        yzmText: '',
        // 
        yzmT: ''
        // 
      }
    },
    mounted () {
      this.ResetMessage()
    },
    methods: {
      // 
      ResetMessage () {
        // localstorage
        let keyUserName = storageKey + 'username'
        if (storage.has(keyUserName)) {
          this.userName = storage.get(keyUserName)
        }
        let keyUserPassWord = storageKey + 'password'
        if (storage.has(keyUserPassWord)) {
          this.passWord = storage.get(keyUserPassWord)
        }
      },
      Login () {
        if (!this.userName || !this.passWord) {
          alert('')
          return null
        }
        if (!this.userName.length > 20) {
          alert('2010')
          return null
        }
        if (!isPassword(this.passWord)) {
          alert('6-16')
          return null
        }
        this.userName = escape(xss(this.userName))
        this.passWord = escape(xss(this.passWord))
        // xss
        if (!this.isRegister) {
          // 
          // if (this.yzmText !== this.yzmT) {
          //   alert('')
          //   this.newYZM()
          //   return
          // }
          axios.post('/users/list', {
            username: this.userName,
            password: this.passWord
          }).then((response) => {
            let res = response.data
            if (res.status === '2' || res.status === '1') {
              this.message = res.msg
            } else if (res.status === '0') {
              this.setCurrentUser(res.result)
              this.setLoginSuccess(true)
              if (this.isRememberUsername) {
                // localstorage
                let keyUserName = storageKey + 'username'
                storage.set(keyUserName, res.result.userName)
              }
              if (this.isRememberPassword) {
                // localstorage
                let keyUserPassWord = storageKey + 'password'
                storage.set(keyUserPassWord, this.passWord)
              }
              alert('' + res.result.userName + '')
              this.isFa()
            }
          })
        } else {
          // 
          if (!isEmail(this.userEmail)) {
            alert('')
            return null
          }
          this.userEmail = encodeURIComponent(xss(this.userEmail))
          if (!this.userName || !this.passWord || !this.userEmail) {
            this.messageText = ''
            return null
          }
          axios.post('/users/register', {
            username: this.userName,
            password: this.passWord,
            useremail: this.userEmail
          }).then((response) => {
            alert('')
            this.isRegister = false
          })
        }
      },
      Register () {
        this.isRegister = !this.isRegister
      },
      isReUn () {
        this.isRememberUsername = !this.isRememberUsername
      },
      isRePw () {
        this.isRememberPassword = !this.isRememberPassword
      },
      newYZM () {
        this.$refs.yzm.newYZM()
      },
      setYZM (str) {
        this.yzmT = str
      }
    },
    watch: {
      isRegister (newValue) {
        if (!newValue) {
          this.ResetMessage()
        } else {
          this.userName = ''
          this.passWord = ''
        }
      },
      isLogin (newVal) {
        if (newVal) {
          this.newYZM()
        }
      }
    },
    components: {
      YanZhengMa
    }
  }
</script>




